.header {
    display: flex;
    justify-content: space-between;
    margin: 10px;
    border: 1px solid black;
}

.nav-items >ul{
    list-style-type: none;
    display: flex;
}

.nav-items >ul >li{
    padding: 10px;
}

.card {
    width: 200px;
    border: 1px solid black;
    padding: 10px;
    margin: 10px;
}


.card > img{
    width: 100%;
}

.shimmer-card {
    height: 300px; 
    width: 200px;
    border: 1px solid black;
    padding: 10px;
    margin: 10px;
    background-color: grey; 
} 

.restuarant-card{
    display: flex;
    flex-wrap: wrap;
}
.shimmer-ui{
    display: flex;
    flex-wrap: wrap;
}

.menu{
    display: flex;
}